/**
 * SEO component that queries for data with
 *  Gatsby's useStaticQuery React hook
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React, {FunctionComponent} from "react"
import {Helmet} from "react-helmet"
import {useStaticQuery, graphql} from "gatsby"

const companyInfo = require('./../../../src/thebakers.json');

const SEO: FunctionComponent<{
  description?: string, image?: string, siteProps?: any, lang?: string, meta?: any[], title?: string, markups?: any[]
}> =
  ({description, image, siteProps, lang, meta, title, markups}) => {

    const pagePath = siteProps.location.pathname;


    if (!siteProps.location.pathname) {
      throw new Error("Pass props with pathname to seo component");
    }

    const metaDescription = description || "";


    if (metaDescription.length < 70 || metaDescription.length > 158) {
      //throw new Error("Meta description should be between 70 and 158." + metaDescription);
    }

    var cano = `https://${companyInfo.primaryDomain}${pagePath}`;

    console.log(`https://${companyInfo.primaryDomain}${image}`);
    return (
      <Helmet
        htmlAttributes={{
          lang: "de",
        }}
        title={title}
        titleTemplate={`%s | ${companyInfo.companyName}`}
        meta={[
          {
            name: `description`,
            content: metaDescription,
          },
          {
            property: `og:title`,
            content: title,
          },
          {
            property: `og:description`,
            content: metaDescription,
          },
          {
            property: `og:url`,
            content: `https://${companyInfo.primaryDomain}${pagePath}`
          },
          {
            property: `og:type`,
            content: `website`,
          },
          {
            name: `twitter:card`,
            content: `summary`,
          },
          {
            name: `twitter:title`,
            content: title,
          },
          {
            name: `twitter:site`,
            content: `https://${companyInfo.primaryDomain}${pagePath}`
          },
          {
            name: `twitter:description`,
            content: metaDescription,
          },
        ].concat(
          image ? [{
            property: `og:image`,
            content: "https://" + companyInfo.primaryDomain + image,
          }, {
            name: `twitter:image`,
            content: "https://" + companyInfo.primaryDomain + image
          }] : []
        )}
      >
        {!!markups && markups.map(i => {
          return (
            <script type="application/ld+json">
              {`${JSON.stringify(i)}`}
            </script>
          )
        })}

        {[{
          "@context": "http://schema.org",
          "@type": "Organization",
          "name": companyInfo.companyName,
          "telephone": companyInfo.telephone,
          "email": companyInfo.mail,
          "url": "https://" + companyInfo.primaryDomain,
          "sameAs": [
            companyInfo.instagram,
            companyInfo.facebook,
            companyInfo.twitter,
            companyInfo.provenexpert,
            companyInfo.linkedin,
            companyInfo.youtube,
          ],
          "aggregateRating": {
            "@type": "AggregateRating",
            "ratingValue": "5",
            "bestRating": "5",
            "ratingCount": "7"
          },
          "address": {
            "@type": "PostalAddress",
            "streetAddress": companyInfo.street,
            "addressRegion": companyInfo.bundesland,
            "postalCode": companyInfo.plz,
            "addressCountry": companyInfo.country_iso
          }
        }].map(meta => {
          return <script key={meta as any} type="application/ld+json">
            {`${JSON.stringify(meta)}`}
          </script>
        })}
        <link rel="canonical" href={cano}/>
      </Helmet>
    )
  }


export default SEO
